@charset "UTF-8";
.container {
  width: 100%;
  height: 55px;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  z-index: 1000;
  border-bottom: #8b8b8b 1px solid; }
  .container .logo {
    margin-left: 100px;
    margin-right: 100px;
    cursor: pointer; }
    .container .logo .icon {
      width: 43px;
      height: 40px; }
    .container .logo .name {
      height: 37px;
      margin-left: 10px; }
  .container .text {
    padding: 0 5px;
    font-family: "Adobe 黑体 Std R"; }
  .container .line {
    width: 0px;
    height: 2px;
    background-color: #4daee7;
    position: absolute;
    margin-top: 10px;
    transition: width 300ms;
    -moz-transition: width 300ms;
    /* Firefox 4 */
    -webkit-transition: width 300ms;
    /* Safari and Chrome */
    -o-transition: width 300ms;
    /* Opera */ }
  .container .itemUnselet {
    cursor: pointer;
    margin: 0 10px;
    font-size: 1em;
    color: #000000;
    position: relative;
    text-decoration: none; }
  .container .itemUnselet:hover .line {
    width: 100%; }
  .container .itemSelected {
    cursor: pointer;
    margin: 0 10px;
    color: #4daee7;
    position: relative;
    text-decoration: none; }
    .container .itemSelected .line {
      width: 100%; }
